<template>
  <x-header :left-options="{showBack: false}">   </x-header>
  <swiper :list="list" :auto="true" :loop="true" style="margin:0px;" :aspect-ratio="1600/2588"></swiper>
  <group title="">
    <scroller lock-x scrollbar-y use-pullup :pullup-config="pullupConfig" height="auto" @pullup:loading="load2">
      <cell v-for="val in newsList" :title="val.title" is-link :inline-desc="val.inline" :link="{path:'/detail/'+val.id}"></cell>
    </scroller>
  </group>
  <toast :show.sync="show2" :time="1500" type="text"></toast>
</template>

<script>
import {Group, Cell, Swiper, Scroller, Toast, XHeader} from 'vux/src/components'

export default {
  components: {
    Group,
    Cell,
    Swiper,
    Scroller,
    Toast,
    XHeader
  },
  data () {
    return {
      // note: changing this line won't causes changes
      // with hot-reload because the reloaded component
      // preserves its current state and we are modifying
      // its initial state.
      list: [{
        url: 'http://jerryshi.com',
        img: 'http://ofx24fene.bkt.clouddn.com/img/test/IMG_1666_Jc.jpg',
        title: ''
      }, {
        url: 'http://jerryshi.com',
        img: 'http://ofx24fene.bkt.clouddn.com/img/test/IMG_0865_Jc.jpg',
        title: ''
      }, {
        url: 'http://jerryshi.com',
        img: 'http://ofx24fene.bkt.clouddn.com/img/test/IMG_1360_Jc.jpg',
        title: ''
      }],
      newsList: [{
        id: '111',
        title: '',
        inline: ': 2016-09-09        99'
      }, {
        id: '222',
        title: '()',
        inline: ': 2016-08-13        64'
      }, {
        id: '333',
        title: '201685',
        inline: ': 2016-08-02        40'
      }, {
        id: '444',
        title: '',
        inline: ': 2016-06-03        14'
      }, {
        id: '555',
        title: '26',
        inline: ': 2016-03-01        113'
      }, {
        id: '666',
        title: '2014',
        inline: ': 2015-11-23        291'
      }],
      pullupConfig: {
        content: '',
        downContent: '',
        upContent: '',
        loadingContent: '...'
      },
      show2: false
    }
  },
  methods: {
    load2 (uuid) {
      setTimeout(() => {
        if (this.newsList.length === 10) {
          this.show2 = true
          this.$broadcast('pullup:done', uuid)
        } else {
          this.newsList.push({
            id: '003',
            title: '',
            inline: ': 2016-06-03        14'
          })
          this.$broadcast('pullup:reset', uuid)
        }
      }, 2000)
    }
  }
}
</script>

<style>
.vux-demo {
  text-align: center;
}

.logo {
  width: 100px;
  height: 100px
}
</style>


